.main-page__wrapper {
  @include desktop-center;
}

.about-company {
  position: relative;
  z-index: -1;
  height: 690px;

  margin-top: -61px;

  background: url("../img/main_fitness.jpg") no-repeat right; 
  
  &::before {
    content: '';

    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;

    background: url("../img/vector-main-train.svg") no-repeat left rgba(0, 0, 0, 0.3);
  }
} 

.about-company_tagline {

position: absolute;
width: 456px;
height: 184px;
left: 30px;
top: 179px;

margin: 0;
padding: 0;

@include title-fonts(42px, 46px);

text-transform: uppercase;
}

.about-company__bottomline {
  position: absolute;
  bottom: 0;
  display: flex;

  padding-left: 33px;
  padding-bottom: 33px;
}

.about-company__tag {
  @include title-fonts;

  width: 352px;

  text-transform: uppercase;
  &--accent {
    color: #FFFE54;
  }
}

.about-company__button {
  margin-left: 218px;
  margin-top: 14px;
}