@mixin section-title($font: #111111, $size:36px, $height: 60px) {
  color: $font;

  font-family: $title-font;
  font-size: $size;
  font-weight: 400;
  line-height: $height;
}

@mixin center {
  margin-right: auto;
  margin-left: auto;
}

@mixin mobile-center {
  max-width: 320px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 20px;
}

@mixin tablet-center {
  max-width: 768px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 30px;
}

@mixin desktop-center {
  max-width: 1280px;
  margin-right: auto;
  margin-left: auto;
}

@mixin desktop-center {
  max-width: 1280px;
  margin-right: auto;
  margin-left: auto;
}


@mixin title-fonts ($size: 16px, $height: 25px) {
font-family: "TRY Grtsk Tera";
font-size: $size;
line-height: $height;

text-transform: uppercase;
}
