.active__wrapper {
  padding: 112px 158px 0;
  
}

.active__title {
  @include title-fonts(30px, 36px);
  padding: 0;
  margin: 0;
  margin-bottom: 47px;
}

.active__photos {
  display: flex;

  justify-content: space-between;

  flex-wrap: wrap;
}

.active__photo {
  position: relative;
  z-index: -1;

  margin-bottom: 4px;

  &--fitness{
    &::before {
      content: " ";
      position: absolute;
      z-index: 2;
      left: 20px;
      top: 20px;

      width: 40px;
      height: 24px;

      background-image: url(../img/icon_fitness.png)
    }
  &::after {
    content: "ФИТНЕС";
    position: absolute;

    @include title-fonts(16px, 22px);
    font-style: normal;
    z-index: 2;

    left: 21px;
    bottom: 32px;
  }
  }

  &--skate{
    &::before {
      content: " ";
      position: absolute;
      z-index: 2;
      left: 20px;
      top: 20px;

      width: 34px;
      height: 29px;

      background-image: url(../img/icon_skate.svg)
    }
  &::after {
    content: "РОЛИКИ";
    position: absolute;

    @include title-fonts(16px, 22px);
    font-style: normal;
    z-index: 2;

    left: 21px;
    bottom: 37px;
  }
  }

  &--summer{
    &::before {
      content: " ";
      position: absolute;
      z-index: 2;
      left: 20px;
      top: 20px;

      width: 42px;
      height: 33px;

      background-image: url(../img/icon_summer.svg)
    }
  &::after {
    content: "ЛЫЖЕРОЛЛЕРЫ";
    position: absolute;

    @include title-fonts(16px, 22px);
    font-style: normal;
    z-index: 2;

    left: 21px;
    bottom: 32px;
  }
  }

  &--skand{
    &::before {
      content: " ";
      position: absolute;
      z-index: 2;
      left: 7px;
      top: 3px;

      width: 60px;
      height: 60px;

      background-image: url(../img/icon_skand.svg)
    }
  &::after {
    content: "СКАНДИНАВСКАЯ ХОДЬБА";
    position: absolute;

    @include title-fonts(16px, 22px);
    font-style: normal;
    z-index: 2;

    left: 21px;
    bottom: 32px;
  }
  }

  &--ski{
    &::before {
      content: " ";
      position: absolute;
      z-index: 2;
      left: 20px;
      top: 22px;

      width: 33px;
      height: 26px;

      background-image: url(../img/icon_ski.svg)
    }
  &::after {
    content: "БЕГОВЫЕ ЛЫЖИ";
    position: absolute;

    @include title-fonts(16px, 22px);
    font-style: normal;
    z-index: 2;

    left: 21px;
    bottom: 32px;
  }
  }
}

.active__photos img {
  border-radius: 20px;
  margin-bottom: 10px;;

  opacity: 0.9;
}