.tariffs__wrapper {
  padding: 112px 158px 0;
}

.tariffs__title {
  @include title-fonts(30px, 36px);
  padding: 0;
  margin: 0;
  margin-bottom: 47px;
}

.tariffs__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.tariffs__activate {
  width: 100%;
  height: 170px;

  padding-top: 75px;
  padding-left: 60px;

  @include title-fonts(16px, 22px);
  font-style: normal;
  color: #7AFBBC;

  background: url("../img/vector-activate.svg") no-repeat -2px,
              url("../img/activate.jpg") no-repeat right #2D2D2D;; 
  border-radius: 2px;
  border: none;
}