.tariff {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-between;

  width: 308px;
  height: 368px;

  padding-top: 19px;
  padding-left: 16px;
  margin-bottom: 15px;

  background: #2D2D2D;
  border-radius: 2px;

  &:hover {
    background: #FFFE54;

    .tariff__title {
      color: #181818;
    }

    .tariff__text {
      color: #181818;
    }

    .tariff__button {
      text-align: center;

      background: #181818;
    }
  }
}

.tariff__title {
  @include title-fonts;

  padding: 0;
  margin: 0;

  color: #FFFE54;

  &--free {
    color: #7AFBBC;
  }
}

.tariff__text {
  display: block;
  width: 258px;

  padding: 0;
  margin: 0;
  margin-top: 25px;

  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 25px;
}

.tariff__button {
  width: 273px;
  height: 50px;

  padding: 0;
  margin-top: auto;
  margin-bottom: 14px;

  @include title-fonts(13px, 23px);
  color: #Ffffff;
  font-style: normal;
  text-align: left;

  background-color: transparent;
  border: none;
  border-radius: 2px;

  }
